<template>
    <div class="user-list">
        <SideBar />
        <div class="iframe">
            <HeaderManagement />
            <BodyManagement />

            <div class="title-section">
                <h2 class="heading-section">Subscribers</h2>
                <span>
                    Email address list of all my subscribers.
                </span>
            </div>

            <ul class="list-group">
                <li class="list-group-item">
                    <div class="flex-section">
                        <div class="actions">
                            <span class="semi-bold">Actions</span>
                        </div>
                        <div class="email-address">
                            <span class="semi-bold">Email Address</span>
                        </div>
                    </div>
                </li>
            </ul>
    
            <ul class="list-group">
                <li
                    class="
                    list-group-item
                    d-flex
                    justify-content-between
                    align-items-center"
                    v-for="user in users"
                    :key="user.id"
                >
                    <div class="flex-section">
                        <div class="actions">
                            <router-link
                                :to="{ path: '/edit-user/'+user.id }"
                                class="btn2 btn-primary2"
                            >
                                <i class="fa-solid fa-user-pen"></i>
                            </router-link>
                            <a href="#" class="btn2 btn-danger2 mx-1" @click="deleteUser(user.id)">
                                <i class="fa-solid fa-user-minus"></i>
                            </a>
                        </div>
                        <div class="email-address">
                            {{ user.email }}
                        </div>
                    </div>
                </li>
            </ul>

            <div class="d-table">
                <router-link to="/users-dump" class="btn btn-primary">Users Dump</router-link>
            </div>
            
        </div>
    </div>
</template>

<script>
import SideBar from "../components/SideBar.vue";
import HeaderManagement from "../components/HeaderManagement.vue";
import BodyManagement from "@/components/BodyManagement.vue";

import usersColRef from '@/firebase'
import { getDocs, doc, deleteDoc } from 'firebase/firestore'

export default {
    name: 'UsersListView',
    components: {
    SideBar,
    HeaderManagement,
    BodyManagement
    },
    data() {
        return {
            users: [],
            selectedDoc: null
        }
    },
    methods: {
        async fetchUsers () {
            const usersSnapShot = await getDocs(usersColRef)
            const users = []
            usersSnapShot.forEach((user) => {
                const userData = user.data()
                userData.id = user.id
                users.push(userData)
            })
            this.users = users
        },
        async deleteUser (id) {
            const userRef = doc(usersColRef, id)
            await deleteDoc(userRef)
            alert('Email address deleted successfully!')
            this.$router.go()
        }
    },
    created () {
        this.fetchUsers()
    }
}
</script>

